.advantage {
  padding: 114px 15px 118px;
  background-color: #f8f9fc;
}

.advantage-wrapper {
  margin: 0 auto;
  max-width: 1160px;
  text-align: center;
}

.advantage h2 {
  margin: 0;
  margin-bottom: 59px;
  font-family: "Bebas Neue", sans-serif;
  font-size: 49px;
  line-height: 49px;
  letter-spacing: 0.03em;
  color: #102654;
  font-weight: normal;
}

.advantage__list {
  margin: 0;
  padding: 0 38px 0 79px;
  list-style: none;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  position: relative;
}

.advantage__item {
  margin-bottom: 78px;
  margin-right: 200px;
  display: flex;
  justify-content: flex-start;
  width: calc(50% - 170px);
  box-sizing: border-box;
  text-align: left;
  position: relative;
}

.advantage__item:first-of-type {
  padding-left: 8px;
}

.advantage__item:first-of-type p {
  padding-right: 2px;
}

.advantage__item:first-of-type .advantage__block-number {
  background: linear-gradient(155deg, #d10a42 10%, #0041ca 45%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.advantage__item:nth-child(even) {
  margin-right: 0;
}

.advantage__item:nth-last-child(-n + 3) {
  margin-bottom: 0;
}

.advantage__item:not(:nth-child(-n + 2)) {
  margin-top: 70px;
  position: relative;
}

.advantage__item-right {
  margin-right: 0;
  width: calc(50% - 160px);
}



.advantage__item-line::before {
    content: "";
    position: absolute;
    top: 166px;
    left: 0;
    width: 100%;
    height: 2px;
    background: rgba(209, 10, 66, 0.2);
  }


.advantage__text {
  padding-top: 4px;
  padding-right: 5px;
}

.advantage__text h3 {
  margin: 0;
  margin-bottom: 6px;
  font-family: $font;
  font-style: normal;
  font-weight: bold;
  font-size: 22px;
  line-height: 150%;
  color: #102654;
}

.advantage__text p {
  margin: 0;
  font-family: $font;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 140%;
  //letter-spacing: -0.01em;
  color: #102654;
}

.advantage__block {
  margin-right: 30px;
}

.advantage__block-number {
  margin: 0;
  font-family: $font;
  font-style: normal;
  font-weight: bold;
  font-size: 71px;
  line-height: 100%;
  background: linear-gradient(165deg, #d10a42 10%, #0041ca 80%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.advantage__block-text {
  margin: 0;
  font-family: $font;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 120%;
  color: #2e35ac;
}

.advantage__cover {
  padding: 44px 34px 44px;
  text-align: center;
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -48%);
  width: 21%;
  background-color: #1c3374;
  color: $white;
  box-sizing: border-box;
}

.advantage__cover h3 {
  margin-bottom: 33px;
  font-size: 26px;
  line-height: 165%;
}

.advantage__cover p {
  font-size: 16px;
  line-height: 140%;
}

.advantage__cover span {
  font-size: 74px;
}

@media (max-width: $tablet) {
  .advantage {
    padding: 96px 45px 94px;
  }

  .advantage h2 {
    margin-bottom: 45px;
    font-size: 42px;
    line-height: 42px;
  }

  .advantage__list {
    padding: 0;
    justify-content: center;
  }

  .advantage__item {
    margin-bottom: 44px;
    margin-right: 30px;
    width: calc(50% - 15px);
  }

  .advantage__item:nth-child(-n + 2) {
    order: -2;
  }

  .advantage__block-number {
    font-size: 58px;
  }

  .advantage__text {
    padding-top: 0;
    padding-right: 5px;
  }

  .advantage__text h3 {
    margin-bottom: 2px;
    font-size: 20px;
  }


  .advantage__item-line::before {
      content: none;
      opacity: 0;
    }
  

  .advantage__item:first-of-type {
    padding-left: 0;
  }

  .advantage__item:first-of-type p {
    padding-right: 0;
  }

  .advantage__item:not(:nth-child(-n + 2)) {
    margin-top: 42px;
    position: relative;
  }

  .advantage__cover {
    padding: 30px 126px 25px;
    order: -1;
    position: relative;
    left: auto;
    top: 3px;
    transform: none;
    width: 65.2%;
  }

  .advantage__cover h3 {
    margin-bottom: 0;
    font-size: 22px;
    line-height: 160%;
  }

  .advantage__cover p {
    font-size: 16px;
    line-height: 132%;
  }
}

@media (max-width: $mobile) {
  .advantage {
    padding: 70px 0 8px;
  }

  .advantage h2 {
    margin-bottom: 44px;
    font-size: 32px;
    line-height: 32px;
  }

  .advantage__list {
    padding: 0;
    flex-direction: column;
    align-items: center;
  }

  .advantage__item {
    padding: 0 15px;
    margin-bottom: 34px;
    margin-right: 0;
    width: 100%;
    display: block;
    text-align: center;
  }

  .advantage__item:first-of-type {
    padding-left: 15px;
  }

  .advantage__item:not(:nth-child(-n + 2)) {
    margin-top: 0;
  }

  .advantage__item:nth-last-child(-n + 3) {
    margin-bottom: 34px;
  }

  .advantage__item:last-of-type {
    margin-bottom: 0;
  }

  .advantage__block {
    margin-right: 0;
    margin-bottom: 7px;
  }

  .advantage__text {
    padding-right: 0;
  }

  .advantage__item:first-of-type .advantage__text p {
    padding: 0 52px;
  }

  .advantage__text p {
    padding: 0 30px;
    padding-left: 38px;
  }

  .advantage__text h3 {
    margin-bottom: 3px;
  }

  .advantage__cover {
    margin-top: -10px;
    margin-bottom: 41px;
    padding: 37px 65px 26px;
    width: 100%;
  }

  .advantage__cover h3 {
    margin-bottom: 10px;
    line-height: 145%;
  }

  .advantage__cover p {
    margin-top: 0;
    line-height: 150%;
  }

  .advantage__item:first-of-type .advantage__block-number {
    background: linear-gradient(155deg, #d10a42 33%, #0041ca 50%);
    background-clip: text;
  }

  .advantage__block-number {
    background: linear-gradient(165deg, #d10a42 33%, #0041ca 62%);
    background-clip: text;
  }
}
