 .wrapp-connection {
   background: linear-gradient(to right, #102654 50%, #ffffff 50%);
   position: relative;

   &::before {
     content: "";
     position: absolute;
     width: 100%;
     height: 1px;
     background-color: #74819c;
     top: 0;
     left: 0;
     z-index: 1;
  }
}

.connection {
  display: flex;
  flex-wrap: wrap;
}

 @media (max-width: $table-width) {
   .wrapp-connection {
     background: none;
     position: relative;
     &::before {
       content: "";
       width: 0;
       height: 0;
       z-index: 0;
     }
   }
 }
