.communication__list {
  list-style: none;
  margin: 0;
  padding: 0;
  padding-left: 60px;
}

.communication__tel,
.communication__mail {
  font-size: 14px;
  line-height: 25px;
  margin: 0;
  width: 110px;
  text-decoration: none;
  color: #102654;
  display: flex;
  flex-wrap: wrap;

  & b {
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
  }
  &:hover {
    text-decoration: none;
    opacity: 0.7;
  }

}

.communication__item {
  &:first-of-type {
    margin-bottom: 16px;
  }
}

@media (max-width: $tablet) {
  .communication__tel,
  .communication__mail {
    font-size: 18px;
    line-height: 37px;
    margin: 0;
    width: 116px;
    text-decoration: none;
    color: #102654;
    display: flex;
    flex-wrap: wrap;

    & b {
      font-weight: bold;
      font-size: 20px;
      line-height: 30px;
    }
  }

  .communication__mail {
    width: 142px;
  }

  .communication__list {
    padding: 0;
  }
}
